<template>
  <div class="login">
    <div class="login-head clearfix">
      <div class="login-text fr" @click="goRegister">登录</div>
    </div>
    <div class="logo">
      <img src="../assets/images/close.png" alt class="auto-img logo-img" />
    </div>
    <div class="register-form">
      <van-form>
        <van-field
          v-model="userLoginInfo.nickName"
          placeholder="请输入昵称"
          :rules="[{ required: true, message: '请填写昵称' }]"
        />
        <van-field
          v-model="userLoginInfo.phone"
          placeholder="请输入手机号"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="userLoginInfo.password"
          :type="ispassword ? 'password' : 'text'"
          placeholder="请输入密码"
          :rules="[{ required: true, message: '请填写密码' }]"
          :right-icon="ispassword ? 'closed-eye' : 'eye-o'"
          @click-right-icon="passwordShow"
        />
        <div style="margin: 25px 16px">
          <van-button
            round
            block
            type="info"
            native-type="submit"
            @click="submitLogin"
            >注 册</van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { validForm } from "../assets/js/validForm";
export default {
  data() {
    return {
      // 注册信息
      userLoginInfo: {
        phone: "",
        nickName: "",
        password: "",
      },
      // 密码显示
      ispassword: true,
      a: [],
    };
  },

  methods: {
    // 密码显示隐藏
    passwordShow() {
      this.ispassword = !this.ispassword;
    },
    //跳转登录
    goRegister() {
      this.$router.push({ name: "Register" });
    },
    //注册
    submitLogin() {
      //获取用户注册信息
      let userInfo = this.userLoginInfo;
      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: "昵称格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确,字母开头且不少于6位",
        },
      };

      if (!validForm.valid(o)) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0,
      });

      setTimeout(() => {
        this.$toast.clear();
        let lay = JSON.parse(localStorage.getItem("Login"));
        //第一次注册
        if (!lay) {
          let login = [];
          login.push(userInfo);
          localStorage.setItem("Login", JSON.stringify(login));
          // 清空注册表单的数据
          for (let key in userInfo) {
            userInfo[key] = "";
          }
          return;
        }
         let isLogin = lay.some((v) => {
          return v.phone == this.userLoginInfo.phone;
        });
        //判断是否被注册过
        if (isLogin) {
          this.$notify({ background: "#157efb", message: "该手机号已被注册" });
          return;
        }
        //后续注册信息
        lay.push(userInfo);
        localStorage.setItem("Login", JSON.stringify(lay));
        // 清空注册表单的数据
        for (let key in userInfo) {
          userInfo[key] = "";
        }
        this.$notify({ background: "#157efb", message: "注册成功" });
      }, 1000);
    },
  },
};
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100%;
  background: url("../assets/images/bg.png") no-repeat;
  background-size: 100% 100%;

  .login-head {
    padding: 15px 10px;
  }

  .login-text {
    font-size: 14px;
    color: #fff;
  }

  .logo {
    text-align: center;
    height: 120px;
    width: 120px;
    margin: 50px auto 10px;
    border-radius: 50%;
    overflow: hidden;
  }

  .register-form {
    padding-top: 25px;
  }

  /deep/ .van-cell {
    background: none;
  }

  /deep/ .van-cell::after {
    border-bottom: 1px solid #00b46d;
  }

  /deep/ .van-field__control {
    color: #fff;
  }

  /deep/ .van-cell.van-field {
    padding: 15px 50px;
  }

  /deep/ .van-button--info {
    background-color: #00b46d;
    border: 0.02667rem solid #00b46d;
  }
}
</style>